
//react calculator 
import { useReducer } from 'react'
import DigitButton from './DigitButton'
import OperatorsButtons from './OperatorsButton'
import './styles.css'
import { type } from '@testing-library/user-event/dist/type'



export const ACTIONS = { ADD_DIGIT: 'add_digit', ADD_OPERATION: 'add_operation', EVALUATION: 'evaluation', CLEAR_DIGITS: 'clear_digits', DELETE_DIGIT: 'delete_digit'}

function reducer(state, {type, payload})
{
    switch(type)
    {
        case ACTIONS.ADD_DIGIT:
            if(payload.digit === "0" && state.current_number === "0" ) return state
            if(payload.digit === "." && state.current_number.includes(".")) return state
            if(state.overwrite)
            {
                return{
                    ...state,
                    current_number: payload.digit,
                    overwrite: false,
                }
            }
            return{
                ...state,
                current_number:`${state.current_number||""}${payload.digit}`
            }
        case ACTIONS.CLEAR_DIGITS:
            return {}
        case ACTIONS.ADD_OPERATION:
            if(state.current_number == null && state.previous_number == null)
            {
                return state;
            }
            if(state.current_number == null)
            {
                return{
                    ...state,
                    operation:payload.operation,
                }
                               
            }
            if (state.previous_number == null)
            {
                return {
                    ...state,
                    operation : payload.operation,
                    previous_number: state.current_number,
                    current_number: null
    
                }    
            }
            return{
                ...state,
                previous_number: evaluate(state),
                operation: payload.operation,
                current_number: null
            }
            case ACTIONS.EVALUATION:
                if(state.current_number == null || state.previous_number == null || state.operation == null)
                {
                    return state
                }
                return{
                    ...state,
                    overwrite: true,
                    previous_number: null,
                    operation: null,
                    current_number: evaluate(state),
                }
            case ACTIONS.DELETE_DIGIT:
                if(state.overwrite)
                {
                    return{
                        ...state,
                        overwrite: false,
                        current_number: null
                    }
                }
                if(state.current_number == null) return state
                if(state.current_number.length === 1)
                {
                    return{
                        ...state,
                        current_number: null
                    }
                }
                return{
                    ...state,
                    current_number: state.current_number.slice(0,-1)
                }





    }
    

}
    

function evaluate({current_number, previous_number, operation})
{
    const prev = parseFloat(previous_number)
    const current = parseFloat(current_number)
    if(isNaN(prev)|| isNaN(current)) return ""
    let computation = ""
    switch(operation){
        case "+":
            computation = prev + current
            break
        case "-":
            computation = prev - current
            break
        case "/":
            computation = prev/current
            break
        case "*":
            computation = prev * current
            break

    }
    return computation.toString()

}

export default function App()
{
    const[{current_number, operation, previous_number}, dispatch] = useReducer(reducer, {})

    return (
    <div className="calculator_grid">
        <div className="output_div">
            <div className="previous_number">
                {previous_number} {operation}
            </div>
            <div className="current_number">{current_number}</div>
        </div>
            <button className="span-two" onClick={()=>dispatch({type: ACTIONS.CLEAR_DIGITS})}>AC</button>
            <button onClick={()=> dispatch({type: ACTIONS.DELETE_DIGIT})}>DEL</button>
            <OperatorsButtons operation='/'dispatch={dispatch}/>
            <DigitButton digit ='1' dispatch={dispatch}/>
            <DigitButton digit ='2' dispatch={dispatch}/>
            <DigitButton digit ='3' dispatch={dispatch}/>
            <OperatorsButtons operation='*'dispatch={dispatch}/>
            <DigitButton digit ='4' dispatch={dispatch}/>
            <DigitButton digit ='5' dispatch={dispatch}/>
            <DigitButton digit ='6' dispatch={dispatch}/>
            <OperatorsButtons operation='+'dispatch={dispatch}/>
            <DigitButton digit ='7' dispatch={dispatch}/>
            <DigitButton digit ='8' dispatch={dispatch}/>
            <DigitButton digit ='9' dispatch={dispatch}/>
            <OperatorsButtons operation='-'dispatch={dispatch}/>
            <DigitButton digit ='.' dispatch={dispatch}/>
            <DigitButton digit ='0' dispatch={dispatch}/>
            <button className="span-two" onClick={()=> dispatch({type: ACTIONS.EVALUATION})}>=</button>


    </div>


    )
    }


       


        
  
